.main-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.control-container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10%;
    flex-wrap: wrap;
}
.button{
    background-color: blueviolet;
    color: white;
    border-style: hidden;
    border-radius: 10px;  
    margin:5%;  
}
.bench-grid-container{
    display: grid;
    grid-template-columns: 40vw 40vw;
    margin: 5%;
    border-radius: 10px;
    background-color: rgb(107, 186, 255);
}
.benchleft{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 10%;
    height:10px;
    width: 100px;
    background-color: rgb(1, 148, 181);
    border-style: solid;
    border-color: rgb(56, 112, 255);
}
.benchright{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 10%;
    height:10px;
    width: 100px;
    background-color: rgb(1, 148, 181);
    border-style: solid;
    border-color: rgb(56, 112, 255);
}
.seat{
    height: 8px;
    width: 8px;
    background-color: white;
}